.chats-nav {
  display: flex;
  position: relative;
  padding: 1rem;
  background-image: linear-gradient(white, white);
  background-size: 100% 0.1rem;
  background-position-y: 100%;
  background-position-x: 0%;
  background-repeat: no-repeat;
}

.chats-nav-btn {
  flex: 1;
  text-align: center;
}

.new-msg-btn {
  position: absolute;
  right: 1rem;
  top: 1rem;
  cursor: pointer;
  width: 25px;
  height: auto;
}

.form-close {
  cursor: pointer;
}

.new-msg-form {
  padding: 2rem;
  background-color: var(--main-theme-color);
  border: solid 1px var(--main-theme-color);
  border-radius: 10px;
  color: white;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  opacity: 0;
  transition: all ease-in 0.5s;
  max-width: 725px;
  position: relative;
  height: 600px;
}

.toggle-msg-form {
  opacity: 1;
}

.new-msg-header {
  display: flex;
  justify-content: space-between;
}

.chat-container {
  background-color: var(--main-accent-color);
  width: 425px;
  height: 475px;
  color: white;
  position: relative;
  display: flex;
  flex-direction: column;
}

.chat-view {
  flex: 1;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 1px;
  overflow-y: scroll;
}

.search-field {
  padding: 0.25rem;
}

.search-results {
  position: absolute;
  top: 2rem;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--main-theme-color);
  color: white;
  display: flex;
  flex-direction: column;
  z-index: 5;
}

.result-card {
  display: flex;
  align-items: center;
  font: inherit;
  padding: 0;
  gap: 1rem;
  text-decoration: none;
  color: white;
  cursor: pointer;
  border: none;
  border-bottom: solid white 1px;
  background-color: var(--main-theme-color);
  outline: none;
  font-style: italic;
}

.new-msg-input-container {
  display: flex;
  gap: 0.5rem;
  justify-content: flex-end;
  padding: 0.25rem;
}

.msg-input {
  flex: 1;
  min-width: none;
}

.chat-card {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem;
  border-bottom: solid 1px white;
  color: white;
  text-decoration: none;
  font-family: inherit;
  position: relative;
}

.chat-card-header {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}

.chat-card-time {
  color: lightgray;
  position: absolute;
  top: 1rem;
  right: 1rem;
}

.chat-view-header {
  display: flex;
  gap: 1rem;
  align-items: center;
  justify-content: center;
  position: relative;
}

.back-btn {
  position: absolute;
  height: 25px;
  width: 25px;
  left: 5px;
  bottom: 10px;
}

.back-btn:hover {
  cursor: pointer;
}

.chat-arrow {
  width: 25px;
  height: 25px;
  position: absolute;
  right: 10px;
  bottom: 25px;
}

.chat-view-container {
  display: flex;
  flex-direction: column;
  flex: auto;
  margin: 0 -2rem -2rem -2rem;
  background-color: var(--main-accent-color);
  height: 80%;
}

.chat-view-messages {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  flex: 1;
  gap: 1px;
  position: relative;
}

.top-fetch-trigger {
  position: absolute;
  top: 0;
}

.chat-view-form {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  border-top: 1px solid var(--main-theme-color);
  background-color: var(--main-theme-color);
}

.chat-view-form-elements {
  display: flex;
  gap: 1rem;
  padding: 1rem;
  width: 100%;
}

.chat-view-form-elements > input {
  flex: auto;
}

.chat-component-view {
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  position: relative;
  flex: 1;
  height: 100%;
}

.chat-component-container {
  width: 75%;
  background-color: var(--main-theme-color);
  color: white;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.chat-view-gif-container {
  display: flex;
  gap: 1rem;
  padding: 1rem;
}

.chat-view-gif {
  width: 200px;
  height: auto;
  border-radius: 20px;
}

.chat-gif-clear {
  cursor: pointer;
}

@media screen and (max-width: 600px) {
  .chat-component-container {
    width: 100%;
  }

  .new-msg-form {
    width: 90%;
  }

  .chat-container {
    width: auto;
  }
}
